import {routePrefix, route, koaRouter} from '../router';
koaRouter.add(UserController);

@routePrefix('user/:id')
export class UserController{
    @route() // http://localhost:3000/user
    async getUser({id}){
        let user = await timeout({id:"01",name:'bowen',xxx:id});
        return user;
    }
    @route('list')
    async getList(){
        let list = await timeout([{id:"01",name:'bowen',xxx:id},{id:"01",name:'bowen',xxx:id}]);
        return list;
    }
}

function timeout(testVal) {
    return new Promise((resolve) => {
        setTimeout(function(){
            resolve(testVal);
        }, 10);
    });
}